import React, { useState, useEffect } from "react";
import {
  Box, AppBar, Toolbar, Typography, Button, Dialog, DialogTitle,
  DialogContent, DialogActions, TextField, Grid, Paper, IconButton, MenuItem
} from "@mui/material";
import { Add as AddIcon, Edit as EditIcon, Delete as DeleteIcon } from "@mui/icons-material";
import { supabase } from "./supabaseClient";

const STATUS_LIST = ["준비", "제판", "출고"];
const PAYMENT_LIST = ["미지급", "지급", "지출"];
const STATUS_COLOR = { 준비: "#BDBDBD", 제판: "#4CAF50", 출고: "#E53935" };
const PAYMENT_COLOR = { 미지급: "#FFA726", 지급: "#1976D2", 지출: "#222" };

// 거래처 오토컴플리트 + 신규등록
function CustomerInput({ value, setValue, customers, reloadCustomers }) {
  const [input, setInput] = useState("");
  const showAdd = input && !customers.some(c => c.name === input);
  async function addCustomer(name) {
    const { error } = await supabase.from("customers").insert([{ name }]);
    if (error) alert(error.message);
    await reloadCustomers();
    setValue(name);
  }
  return (
    <>
      <TextField
        label="거래처"
        value={value}
        onChange={e => {
          setInput(e.target.value);
          setValue(e.target.value);
        }}
        margin="dense"
        fullWidth
        list="customers"
        autoComplete="off"
      />
      <datalist id="customers">
        {customers.map(c => <option key={c.id} value={c.name} />)}
      </datalist>
      {showAdd &&
        <Button
          size="small"
          onClick={() => {
            addCustomer(input);
            setInput("");
          }}
        >"{input}" 신규등록</Button>
      }
    </>
  );
}

// 작업내용+단가 오토컴플리트 + 신규등록 + 자동단가입력
function ProductInput({ value, setValue, products, reloadProducts, price, setPrice }) {
  const [input, setInput] = useState("");
  const showAdd = input && !products.some(p => p.name === input);

  // 작업내용 선택시 단가 자동입력
  useEffect(() => {
    const found = products.find(p => p.name === value);
    if (found) setPrice(found.price);
  }, [value, products, setPrice]);

  async function addProduct(name, priceValue) {
    const { error } = await supabase.from("products").insert([{ name, price: priceValue }]);
    if (error) alert(error.message);
    await reloadProducts();
    setValue(name);
    setPrice(priceValue);
  }

  return (
    <>
      <TextField
        label="작업내용"
        value={value}
        onChange={e => {
          setInput(e.target.value);
          setValue(e.target.value);
        }}
        margin="dense"
        fullWidth
        list="products"
        autoComplete="off"
      />
      <datalist id="products">
        {products.map(p => <option key={p.id} value={p.name} />)}
      </datalist>
      {showAdd &&
        <Button
          size="small"
          onClick={async () => {
            const priceValue = Number(prompt("신규 단가 입력", "0")) || 0;
            await addProduct(input, priceValue);
            setInput("");
          }}
        >"{input}" 신규등록</Button>
      }
    </>
  );
}

// 입력폼
function WorkForm({ open, onClose, onSave, edit, customers, reloadCustomers, products, reloadProducts }) {
  const isEdit = !!edit;
  const [date, setDate] = useState(edit?.date || new Date().toISOString().slice(0, 10));
  const [customer, setCustomer] = useState(edit?.customer || "");
  const [product, setProduct] = useState(edit?.product || "");
  const [qty, setQty] = useState(edit?.qty || 1);
  const [price, setPrice] = useState(edit?.price || 0);
  const [status, setStatus] = useState(edit?.status || STATUS_LIST[0]);
  const [payment, setPayment] = useState(edit?.payment || PAYMENT_LIST[0]);
  const amount = qty * price;

  useEffect(() => {
    if (!open) return;
    setDate(edit?.date || new Date().toISOString().slice(0, 10));
    setCustomer(edit?.customer || "");
    setProduct(edit?.product || "");
    setQty(edit?.qty || 1);
    setPrice(edit?.price || 0);
    setStatus(edit?.status || STATUS_LIST[0]);
    setPayment(edit?.payment || PAYMENT_LIST[0]);
  }, [open, edit]);

  return (
    <Dialog open={open} onClose={onClose} fullWidth>
      <DialogTitle>{isEdit ? "작업 수정" : "작업 등록"}</DialogTitle>
      <DialogContent>
        <TextField
          type="date"
          label="날짜"
          value={date}
          onChange={e => setDate(e.target.value)}
          margin="dense"
          fullWidth
          InputLabelProps={{ shrink: true }}
        />
        <CustomerInput value={customer} setValue={setCustomer} customers={customers} reloadCustomers={reloadCustomers} />
        <ProductInput value={product} setValue={setProduct} products={products} reloadProducts={reloadProducts} price={price} setPrice={setPrice} />
        <Grid container spacing={1}>
          <Grid item xs={4}>
            <TextField
              label="수량"
              type="number"
              value={qty}
              onChange={e => setQty(Number(e.target.value))}
              margin="dense"
              fullWidth
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              label="단가"
              type="number"
              value={price}
              onChange={e => setPrice(Number(e.target.value))}
              margin="dense"
              fullWidth
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              label="금액"
              value={amount}
              margin="dense"
              fullWidth
              InputProps={{ readOnly: true }}
            />
          </Grid>
        </Grid>
        <Grid container spacing={1} sx={{ mt: 0.5 }}>
          <Grid item xs={6}>
            <TextField
              select label="상태"
              value={status}
              onChange={e => setStatus(e.target.value)}
              margin="dense"
              fullWidth
            >
              {STATUS_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
            </TextField>
          </Grid>
          <Grid item xs={6}>
            <TextField
              select label="수입"
              value={payment}
              onChange={e => setPayment(e.target.value)}
              margin="dense"
              fullWidth
            >
              {PAYMENT_LIST.map(s => <MenuItem key={s} value={s}>{s}</MenuItem>)}
            </TextField>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>취소</Button>
        <Button
          onClick={() => {
            if (!customer || !product) return alert("거래처, 작업내용 입력");
            onSave({
              date, customer, product, qty, price, amount, status, payment
            });
            onClose();
          }}
          variant="contained"
        >저장</Button>
      </DialogActions>
    </Dialog>
  );
}

// 박스형 리스트
function SalesList({ sales, onEdit, onDelete }) {
  return (
    <Box>
      {sales.map((w, idx) => (
        <Paper
          key={w.id}
          sx={{
            mb: 1, px: 1.2, py: 1.2, borderRadius: 2, boxShadow: 2,
            display: "flex", alignItems: "center", justifyContent: "space-between", flexWrap: "wrap"
          }}
        >
          <Box sx={{ flex: 2, minWidth: 0 }}>
            <Typography variant="subtitle2" noWrap color="text.secondary">{w.date}</Typography>
            <Typography variant="subtitle1" fontWeight={700} noWrap>
              {w.customer}
              <span style={{ fontWeight: 400, color: "#888", marginLeft: 5 }}>({w.product})</span>
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {w.qty} x {w.price.toLocaleString()}원 = <b>{w.amount.toLocaleString()}원</b>
            </Typography>
          </Box>
          <Box sx={{ flex: 1, textAlign: "right" }}>
            <Box>
              <span style={{
                background: STATUS_COLOR[w.status],
                color: "#fff", borderRadius: 8, fontWeight: 700, fontSize: 12, padding: "1px 8px"
              }}>{w.status}</span>
            </Box>
            <Box mt={0.5}>
              <span style={{
                background: PAYMENT_COLOR[w.payment],
                color: "#fff", borderRadius: 8, fontWeight: 700, fontSize: 12, padding: "1px 8px"
              }}>{w.payment}</span>
            </Box>
            <Box>
              <IconButton size="small" onClick={() => onEdit(w)}>
                <EditIcon fontSize="small" />
              </IconButton>
              <IconButton size="small" color="error" onClick={() => onDelete(w)}>
                <DeleteIcon fontSize="small" />
              </IconButton>
            </Box>
          </Box>
        </Paper>
      ))}
      {sales.length === 0 &&
        <Box sx={{ p: 2, color: "#888", textAlign: "center" }}>자료 없음</Box>
      }
    </Box>
  );
}

export default function App() {
  const [sales, setSales] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [products, setProducts] = useState([]);
  const [tab, setTab] = useState("진행중");
  const [formOpen, setFormOpen] = useState(false);
  const [editing, setEditing] = useState(null);
  const [loading, setLoading] = useState(true);

  // fetch functions
  async function fetchSales() {
    setLoading(true);
    let q = supabase.from("barun").select("*").order("id", { ascending: false });
    if (tab === "진행중") q = q.in("status", ["준비", "제판"]);
    if (tab === "완료") q = q.eq("status", "출고");
    if (tab === "미지급") q = q.eq("payment", "미지급");
    const { data, error } = await q;
    if (error) alert(error.message);
    setSales(data || []);
    setLoading(false);
  }
  async function fetchCustomers() {
    const { data, error } = await supabase.from("customers").select("*").order("id", { ascending: false });
    if (error) alert(error.message);
    setCustomers(data || []);
  }
  async function fetchProducts() {
    const { data, error } = await supabase.from("products").select("*").order("id", { ascending: false });
    if (error) alert(error.message);
    setProducts(data || []);
  }

  useEffect(() => { fetchSales(); }, [tab]);
  useEffect(() => { fetchCustomers(); fetchProducts(); }, []);

  // 저장/수정
  async function handleSave(data) {
    if (editing) {
      const { error } = await supabase.from("barun").update(data).eq("id", editing.id);
      if (error) alert(error.message);
    } else {
      const { error } = await supabase.from("barun").insert([data]);
      if (error) alert(error.message);
    }
    setEditing(null);
    fetchSales();
  }

  // 삭제
  async function handleDelete(w) {
    if (!window.confirm("삭제하시겠습니까?")) return;
    const { error } = await supabase.from("barun").delete().eq("id", w.id);
    if (error) alert(error.message);
    fetchSales();
  }

  const unpaidSum = sales.filter(w => w.payment === "미지급").reduce((a, b) => a + (b.amount || 0), 0);
  const tabs = ["진행중", "완료", "미지급"];

  return (
    <Box sx={{ bgcolor: "#f7f8fa", minHeight: "100vh", pb: 6 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>매출·작업 관리</Typography>
          <Button color="inherit" startIcon={<AddIcon />} onClick={() => { setEditing(null); setFormOpen(true); }}>
            등록
          </Button>
        </Toolbar>
      </AppBar>
      <Box sx={{ display: "flex", mt: 1, mb: 1 }}>
        {tabs.map(t =>
          <Button
            key={t}
            onClick={() => setTab(t)}
            variant={tab === t ? "contained" : "text"}
            sx={{ flex: 1, fontWeight: 700 }}
          >{t}</Button>
        )}
      </Box>
      <Box sx={{ px: { xs: 1, sm: 4 } }}>
        {loading ? <Box sx={{ p: 2, color: "#888" }}>로딩중...</Box> :
          <SalesList
            sales={sales}
            onEdit={w => { setEditing(w); setFormOpen(true); }}
            onDelete={handleDelete}
          />
        }
      </Box>
      {tab === "미지급" &&
        <Box sx={{ textAlign: "right", fontWeight: 700, m: 2 }}>
          미지급 합계: {unpaidSum.toLocaleString()} 원
        </Box>
      }
      <WorkForm
        open={formOpen}
        onClose={() => setFormOpen(false)}
        onSave={handleSave}
        edit={editing}
        customers={customers}
        reloadCustomers={fetchCustomers}
        products={products}
        reloadProducts={fetchProducts}
      />
    </Box>
  );
}
